#about {
  background-color: #fff;
  color: #144272;
  margin: 80px;
  position: relative;
}

.container {
  max-width: 1180px;
  margin: auto;
}

.about-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-content-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  color: #144272;
  margin-right: 100px;
}

.about-content-left h5 {
  font-size: 1.2rem;
  margin-bottom: 5px;
}

.about-content-left h1 {
  font-size: 2.5rem;
  margin: 5px 0px;
}

.about-content-left p {
  line-height: 1.6;
  font-size: 1.2rem;
  margin: 5px 0px;
  text-align: left;
}

.about-content-right {
  flex: 1;
  border: solid 5px;
  border-radius: 10px;
  margin: 20px;
  height: 550px;
  position: relative;
}

.about-btn {
  width: 50%;
  margin: 10px 0px;
  border-radius: 5px;
  font-size: 1.4rem;
  border: none;
  background-color: #144272;
  color: #fff;
  padding: 10px;
}

.about-content-right img {
  width: 100%;
  display: block;
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 10px;
  height: 100%;
  transition: all 0.3s ease-in-out;
  object-fit: cover;
}

.about-content-right img:hover {
  top: 0px;
  right: 0px;
  border-radius: 3px;
}

.wave-2 {
  display: block;
  position: absolute;
  bottom: -142px;
  right: -80px;
  height: 80px;
  width: 62%;
  background-color: #144272;
  transform: scale(1, 3);
  clip-path: ellipse(50% 30% at 50% 43%);
}

@media only screen and (max-width: 750px) {
  .about-container {
    flex-direction: column;
    height: 800px;
    margin-bottom: 100px;
  }

  .about-content-right {
    width: 100%;
    border: solid 10px;
    border-radius: 20px;
    margin: 20px;
    height: 650px;
    position: relative;
  }

  .about-content-right .images {
    width: 100%;
    height: 350px;
    display: block;
    position: absolute;
    top: 20px;
    border-radius: 20px;
  }

  .wave-2 {
    display: block;
    position: absolute;
    bottom: -162px;
    right: -50px;
    height: 80px;
    width: 65%;
    background-color: #144272;
    transform: scale(1, 4);
    clip-path: ellipse(50% 30% at 50% 43%);
  }
}

@media only screen and (max-width: 750px) {
  .about-content-left {
    align-items: center;
    justify-content: center;
    color: #144272;
    text-align: center;
    margin-right: 0px;
  }

  .about-content-left h5 {
    font-size: 1rem;
    margin-bottom: 5px;
  }

  .about-content-left h1 {
    font-size: 2rem;
  }

  .about-content-left p {
    line-height: 1.4;
    font-size: 1rem;
    margin: 15px 0px;
  }

  .about-btn {
    width: 50%;
    margin: 10px 0px;
    border-radius: 5px;
    font-size: 1.1rem;
  }

  .wave-2 {
    display: block;
    position: absolute;
    bottom: -202px;
    right: -85px;
    height: 65px;
    width: 80%;
    background-color: #144272;
    transform: scale(1, 6);
  }

  .about-content-right img:hover {
    top: 0px;
    right: 0px;
    border-radius: 10px;
    height: 100%;
    object-fit: fill;
  }
}
